

import useAxios from "@/Components/Axios/useAxios";

export default  function CarDetails({ params }) {
  const { id } = params;
  const axios = useAxios();

  let car = null;

  try {
    const res =  axios.get(`/car-selling/${id}`);
    car = res.data;
  } catch (err) {
    console.log(err.message);
  }

  if (!car) {
    return <div className="p-10">Car Not Found</div>;
  }

  return(
    <div>

    </div>
  )
}
